.about-card-container {
  align-items: center;
  background-color: rgba(220, 230, 247, 0.2);
  border-radius: 0.6rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  margin: 15px 0;
  min-height: calc(100vh - 310px);
}
.about-card-container .about-card {
  align-items: center;
  background-color: #7eb2dd;
  border-radius: 0.6rem;
  box-shadow: 1px 1px 4px rgba(18, 20, 28, 0.7);
  display: flex;
  flex-direction: column;
  margin: 15px 5px;
  max-width: 46%;
  min-width: 158px;
}
@media screen and (min-width: 501px) {
  .about-card-container .about-card {
    max-width: 30%;
  }
}
@media screen and (min-width: 801px) {
  .about-card-container .about-card {
    max-width: 220px;
  }
}
@media (max-width: 375px) {
  .about-card-container .about-card {
    min-width: 140px;
  }
}
.about-card-container .about-card .about-card-header {
  align-items: center;
  background-color: #7eb2dd;
  color: #dce6f7;
  display: flex;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  justify-content: center;
  margin-bottom: 0;
  padding: 3px 0;
  width: 100%;
}
@media (max-width: 375px) {
  .about-card-container .about-card .about-card-header {
    font-size: 15px;
  }
}
.about-card-container .about-card .about-card-hr {
  border-top: 1px solid rgba(58, 64, 90, 0.3);
  display: flex;
  justify-content: center;
  width: 75%;
}
.about-card-container .about-card .about-card-sub-header {
  align-items: center;
  background-color: #7eb2dd;
  color: #dce6f7;
  display: flex;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  justify-content: center;
  padding: 3px 0;
  width: 100%;
}
.about-card-container .about-card .about-card-image {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  margin: 0;
  -o-object-fit: cover;
     object-fit: cover;
  padding: 0;
  width: 100%;
}
.about-card-container .about-card .about-card-link-section {
  background-color: #3a405a;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  width: 100%;
}
.about-card-container .about-card .about-card-link-section .about-card-link {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 5px;
  width: 100%;
}
.about-card-container .about-card .about-card-link-section .about-card-link .about-card-link-img {
  opacity: 0.6;
  width: 32px;
}
